@font-face {
  font-family: "Roboto";
  src: url("./Roboto-Regular.ttf");
}
* {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  box-sizing: border-box;
}

body {
  background-color: #f5f7fc;
}

.app {
  padding: 50px 0 25px;
}

.container {
  max-width: 1350px;
  margin: 0 auto;
}

.flex {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.menu {
  color: white;
}
.menu-inner {
  padding: 0 50px 30px;
}
.menu-select {
  width: 100%;
  background-color: rgb(175, 146, 235);
  border-radius: 10px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  transition: 0.2s;
  cursor: pointer;
}
.menu-select:not(:last-child) {
  margin-bottom: 15px;
}
.menu-select.drop-false {
  opacity: 0.5;
}
.menu-select.status-true {
  opacity: 1;
}
.menu-select__title {
  padding: 10px 50px;
  font-size: 28px;
}
.menu-select__title:not(:last-child) {
  width: 30%;
  padding: 10px 0 10px 50px;
  border-right: 1px solid rgba(255, 255, 255, 0.5);
  justify-content: left;
}
.menu-select__title:not(:first-child) {
  width: 70%;
  font-weight: 400;
  justify-content: center;
}
.menu-select-drop {
  flex-wrap: wrap;
  justify-content: center;
}
.menu-select-drop__title {
  width: 312.5px;
  font-size: 22px;
  justify-content: center;
  font-weight: 400;
  padding: 5px;
  border-left: 1px solid rgba(255, 255, 255, 0.5);
  border-right: 1px solid rgba(255, 255, 255, 0.5);
  transition: 0.2s;
  opacity: 0.5;
  cursor: pointer;
}
.menu-select-drop__title:hover {
  opacity: 1;
}
.menu-select-drop__title.active {
  font-weight: 700;
  opacity: 1;
}
.menu__btn {
  width: 100%;
  background-color: rgba(175, 146, 235, 0.8);
  opacity: 0.8;
  border-radius: 10px;
  border: none;
  outline: none;
  color: white;
  font-size: 18px;
  padding: 15px 0;
  transition: 0.2s;
  cursor: pointer;
}
.menu__btn:hover {
  background-color: rgb(175, 146, 235);
}
.menu__btn[disabled] {
  background-color: rgba(175, 146, 235, 0.8);
  cursor: default;
  opacity: 0;
}

.timetable-inner {
  align-items: flex-start;
  justify-content: left;
  flex-wrap: wrap;
}
.timetable-day {
  width: 635px;
  margin: 20px;
  padding: 10px 15px;
  border: 1px solid rgba(0, 0, 0, 0.25);
}
.timetable-day__title {
  font-size: 24px;
  margin-bottom: 10px;
}
.timetable-day-item {
  padding: 8px 0;
}
.timetable-day-item:not(:last-child) {
  border-bottom: 1px solid rgba(0, 0, 0, 0.25);
}
.timetable-day-item__title {
  max-width: 50%;
}
.timetable-day-item__title.clickable {
  color: cornflowerblue;
  transition: 0.2s;
  cursor: pointer;
}
.timetable-day-item__title.clickable:hover {
  text-shadow: 0 0 0.5px cornflowerblue;
}
.timetable-day-item-info {
  justify-content: right;
  flex-wrap: wrap;
  text-align: right;
}
.timetable-day-item-info__title {
  padding: 2.5px 10px;
  border-radius: 20px;
  text-align: center;
  background-color: #AF92EB;
  color: white;
  margin: 5px 10px 5px 0;
}

.modal {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(2px);
}
.modal-day-tasks {
  max-width: 750px;
  margin: 0 auto;
  padding: 25px;
  border-radius: 25px;
  background-color: #f5f7fc;
  border: 1px solid rgba(0, 0, 0, 0.25);
}
.modal-day-tasks__title {
  text-align: center;
  margin-bottom: 25px;
}
.modal-day-tasks-item {
  padding-top: 10px;
}
.modal-day-tasks-item:not(:last-child) {
  border-bottom: 1px solid rgba(0, 0, 0, 0.25);
}
.modal-day-tasks-item__title {
  font-weight: 400;
}
.modal-day-tasks-item__text {
  display: block;
  margin: 10px 0;
  color: cornflowerblue;
  text-decoration: none;
  transition: 0.2s;
}
.modal-day-tasks-item__text:hover {
  text-shadow: 0 0 0.5px cornflowerblue;
}

.footer__copyright {
  text-align: center;
}

@media screen and (max-width: 1050px) {
  .menu-select__title {
    padding: 10px !important;
  }
}
@media screen and (max-width: 900px) {
  .menu-select-inner {
    display: block;
  }
  .menu-select__title {
    justify-content: center !important;
    width: 100% !important;
    border: none !important;
  }
  .menu-select__title:first-child {
    border-bottom: 1px solid rgba(255, 255, 255, 0.5) !important;
  }
}
@media screen and (max-width: 1350px) {
  .timetable-day {
    width: 100%;
  }
}
@media screen and (max-width: 450px) {
  .menu-select__title {
    font-size: 18px;
  }
  .timetable-day__title {
    font-size: 18px;
  }
  .timetable-day-item__title {
    font-size: 14px;
  }
  .timetable-day-item-info__title {
    font-size: 14px;
  }
}

