@font-face {
  font-family: "Roboto";
  src: url("./Roboto-Regular.ttf");
}

* {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  box-sizing: border-box;
}

body{
  background-color: #f5f7fc;
}

.app{
  padding: 50px 0 25px;
}

.container{
  max-width: 1350px;
  margin: 0 auto;
}

.flex{
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.menu{
  color: white;

  &-inner{
    padding: 0 50px 30px;
  }

  &-select{
    width: 100%;
    //background-color: rgba(175, 146, 235, .5);
    background-color: rgba(175, 146, 235, 1);
    border-radius: 10px;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
    transition: .2s;
    cursor: pointer;

    &:not(:last-child){
      margin-bottom: 15px;
    }

    &.drop-false{
      opacity: .5;
    }

    &.status-true{
      opacity: 1;
    }

    &__title{
      padding: 10px 50px;
      font-size: 28px;

      &:not(:last-child){
        width: 30%;
        padding: 10px 0 10px 50px;
        border-right: 1px solid rgba(255, 255, 255, 0.5);
        justify-content: left;
      }

      &:not(:first-child){
        width: 70%;
        font-weight: 400;
        justify-content: center;
      }
    }

    &-drop{
      flex-wrap: wrap;
      justify-content: center;

      &__title{
        //width: 25%;
        width: 312.5px;
        font-size: 22px;
        justify-content: center;
        font-weight: 400;
        padding: 5px;
        border-left: 1px solid rgba(255, 255, 255, .5);
        border-right: 1px solid rgba(255, 255, 255, .5);
        transition: .2s;
        opacity: .5;
        cursor: pointer;

        &:hover{
          opacity: 1;
        }

        &.active{
          font-weight: 700;
          opacity: 1;
        }

        //&:not(:last-child){
        //  border-right: 1px solid rgba(255, 255, 255, .5);
        //}
        //
        //&:not(:first-child){
        //  border-left: 1px solid rgba(255, 255, 255, .5);
        //}
      }
    }
  }

  &__btn{
    width: 100%;
    background-color: rgba(175, 146, 235, .8);
    opacity: 0.8;
    border-radius: 10px;
    border: none;
    outline: none;
    color: white;
    font-size: 18px;
    padding: 15px 0;
    transition: .2s;
    cursor: pointer;

    &:hover{
      background-color: rgba(175, 146, 235, 1);
    }

    &[disabled]{
      background-color: rgba(175, 146, 235, .8);
      cursor: default;
      opacity: 0;
    }
  }
}

.timetable{
  &-inner{
    align-items: flex-start;
    justify-content: left;
    flex-wrap: wrap;
  }

  &-day{
    width: 635px;
    margin: 20px;
    padding: 10px 15px;
    border: 1px solid rgba(0, 0, 0, 0.25);

    &__title{
      font-size: 24px;
      margin-bottom: 10px;
    }

    &-item{
      padding: 8px 0;

      &:not(:last-child){
        border-bottom: 1px solid rgba(0, 0, 0, 0.25);
      }

      &__title{
        max-width: 50%;

        &.clickable{
          color: cornflowerblue;
          transition: .2s;
          cursor: pointer;

          &:hover{
            text-shadow: 0 0 .5px cornflowerblue;
          }
        }
      }

      &-info{
        justify-content: right;
        flex-wrap: wrap;
        text-align: right;

        &__title{
          padding: 2.5px 10px;
          border-radius: 20px;
          text-align: center;
          background-color: #AF92EB;
          color: white;
          margin: 5px 10px 5px 0;

          //&:not(:last-child){
          //  margin-right: 10px;
          //}
        }
      }
    }
  }
}

.modal{
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, .8);
  backdrop-filter: blur(2px);

  &-day{
    &-tasks{
      max-width: 750px;
      margin: 0 auto;
      padding: 25px;
      border-radius: 25px;
      background-color: #f5f7fc;
      border: 1px solid rgba(0, 0, 0, 0.25);

      &__title{
        text-align: center;
        margin-bottom: 25px;
      }

      &-item{
        padding-top: 10px;

        &:not(:last-child){
          border-bottom: 1px solid rgba(0, 0, 0, 0.25);
        }

        &__title{
          font-weight: 400;
        }

        &__text{
          display: block;
          margin: 10px 0;
          color: cornflowerblue;
          text-decoration: none;
          transition: .2s;

          &:hover{
            text-shadow: 0 0 .5px cornflowerblue;
          }
        }
      }
    }
  }
}

.footer{
  &__copyright{
    //position: fixed;
    //bottom: 20px;
    //left: 0;
    //width: 100%;
    text-align: center;
  }
}

@media screen and (max-width: 1050px){
  .menu{
    &-select{
      &__title{
        padding: 10px !important;
      }
    }
  }
}

@media screen and (max-width: 900px){
  .menu{
    &-select{
      &-inner{
        display: block;
      }

      &__title{
        justify-content: center !important;
        //width: 100% !important;
        width: 100% !important;
        border: none !important;

        &:first-child{
          border-bottom: 1px solid rgba(255, 255, 255, 0.5) !important;
        }
      }
    }
  }
}

@media screen and (max-width: 1350px){
  .timetable{
    &-day{
      width: 100%;
    }
  }
}

@media screen and (max-width: 450px) {
  .menu{
    &-select{
      &__title{
        font-size: 18px;
      }
    }
  }

  .timetable{
    &-day{
      &__title{
        font-size: 18px;
      }

      &-item{
        &__title{
          font-size: 14px;
        }

        &-info{
          &__title{
            font-size: 14px;
          }
        }
      }
    }
  }
}